@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;1,300;1,400&family=Plus+Jakarta+Sans:wght@300;400&family=Prata&family=Quattrocento&family=Quicksand:wght@300;400&display=swap');




/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* Base Styles */

html {
    scroll-behavior: smooth;
    scroll-padding-top: var(--scroll-padding, 50px)
}


:root {
        --clr-light-grey: #C0C9D0;
        --clr-cream: #E6DDDE;
        --clr-red: #F9585C;
        --fnt-title:'Quicksand', sans-serif;
        --fnt-p: 'Quicksand', sans-serif;

}



body {
    font-family: var(--fnt-p);
    width: 100%;
    background-color: var(--clr-cream);

    @media  screen and (max-width: 1200px) {
        max-width: 1200px;
    }
    
}


a {
    color: black;
     text-decoration: none;
     transition: 500ms;

     &:visited {
         color: black;
     }


     &:hover{
         color: var(--clr-red)
     }
}
strong {
    font-size: 3rem;
}

span {
    color: var(--clr-red)

}


.portfolio__container-main {
   display: grid;
   grid-template-areas: 
   'header header header header'
   'main main main main'
   'footer footer footer footer';
    width: 100%;


    @media screen and (max-width: 1200px) {
        display: flex;
        flex-flow: column nowrap;
        max-width: 1200px;
    }
}



/*header style */
header {
    display: flex;
    flex-flow: row nowrap;
    width: 50%;
    align-items: flex-end;
    position: fixed;
    top: 0;
    z-index: 10;
    margin-left: 50%;
    
    @media screen and (max-width: 1200px){
        width: 100%;
        margin: 0px;
    }




}

/* Nav Style */

nav {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin-right: 25px;
       

       
        

        .nav-links {
            background-color: var(--clr-cream);
            width: 60%;
            padding-top:10px;
            padding-bottom: 10px;
            display: flex;
            margin-left: 30%;
            justify-content: space-around;
            flex-flow: row nowrap;
            font-size: 1.1rem;
            border-radius: 0 0 .5rem .5rem;
            box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.174);



            a {
                padding: 5px;


                &:visited {
                    color: black;
                }
           
           
                &:hover{
                    color: var(--clr-red)
                }

            }
        }


        @media screen and (max-width: 1200px) {
            .nav-links {
                margin: 0;
                display: none;
            }
        }


        @media screen and (max-width: 1200px) {
            margin: 0 auto;
            max-width: 1200px;
            background-color: var(--clr-cream);
            padding-bottom: 5px;
            box-shadow: 0px 0px 2px 4px rgba(0, 0, 0, 0.039)
        }
}

/*mobile navigation */

.hamburger {
    position: relative;
    display: none;
    width: 24px;
    height: 24px;
    transition: all 500ms;
    cursor: pointer;
    background-color: var(--clr-cream);
    border: none;
    margin-left: 60%;
    box-sizing: border-box;


    



    .hamburger-top, .hamburger-middle, .hamburger-bottom {
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 2px;
        background: var(--clr-red);
        transform: rotate(0);
        transition: all 500ms ease-in-out;
    }


    .hamburger-middle {
        transform: translateY(7px);


    }

    .hamburger-bottom {
        transform: translateY(14px);
        

    }

    @media screen and (max-width: 1200px){
        display: block;
    }


}


.open {
    transform: rotate(90deg) translateY(0px);

}

.open .hamburger-top {
    transform: rotate(45deg) translateY(6px) translate(6px);

}


.open .hamburger-middle {
    display: none;

}

.open .hamburger-bottom {
    transform: rotate(-45deg) translateY(6px) translate(-6px);
}


@media screen and (max-width: 1200px) {
    .mobile-menu {
        display: hidden;
    }
}


 #logo {

    display: none;
    opacity: 0;
    font-size: 2rem;

    transition: 500ms ease-in;
    width: 50px;

    @media screen and (max-width: 1200px) {
