/* Image Box Styles */
.img-box {
    display: flex;
    width: 60%;

    @media screen and (max-width: 1200px){
        width: 100%;
        flex-flow: column nowrap;


        
    }


}

.big-img {
    width: 50%;
    height: 75vh;

    position: absolute;

    transition: 500ms;

    &:hover {
        transform: scale(1.1);
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7;
    }


    @media screen and (max-width: 1200px){
        width: 100%;
        height: 50%;
        z-index: 0;
        margin: 0 auto;
        position: relative;

        &:hover {
            transform: scale(1);
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7;
        }

    }
}

.medium-img {
    width: 35%;
    height: 35vh;
    z-index: 2;
    position: relative;
    margin-left: 525px;
    transform: translateX(85px);    

    transition: 500ms;

    &:hover {
        transform: scale(1.4) translateY(50px);
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7;

    }

    @media screen and (max-width: 1200px){
        width: 100%;
        height: 75%;
        z-index: 0;
        margin: 0 auto;
        transform: translateX(0px);

        transform: scale(1.1);
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7; 
        
    }

}

.small-img {
    width: 20%;
    height: 20vh;
    z-index: 5;
    position: relative;

    margin-top: 69vh;

    transition: 500ms;

    &:hover {
        transform: scale(2) translateX(60px);
        
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7;
    }

    @media screen and (max-width: 1200px){
        width: 100%;
        height: 75%;
        margin: 0 auto;
        z-index: 0;


        &:hover {
            transform: scale(1) translateX(0px);
            
            box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
            z-index: 7;
        }
        
    }

}


